import React from 'react'

const AddTripButton = (props) => {
    return <button onClick={props.nextPage} style={{height: '13px'}}>
        <div className={props.getState === props.title? "navigation-button active" : "navigation-button"} />
        {
            props.getState === props.title && (
                <div className="navigation-text">{props.title}</div>
            )
        }
    </button>

}

export default AddTripButton
