import React, {useLayoutEffect, useState} from "react";
import {gql, useMutation} from "@apollo/client";
import anime from "animejs";

const SUBMIT_FORM = gql`
    mutation FormSubmission($budget :String, $companyName :String, $country :String, $email :String, $interested :String, $message :String, $name :String, $phoneNumber :String, $website :String) {
      createSubmission(
        input: {
          clientMutationId: "1",
          budget: $budget,
          companyName: $companyName,
          country: $country,
          email: $email,
          interested: $interested,
          message: $message,
          name: $name,
          phoneNumber: $phoneNumber,
          website: $website
        }
      ) {
        success
      }
    }
`;

export default function LetsTalk() {
    const [budget, setBudget] = useState("")
    const [companyName, setCompanyName] = useState("")
    const [country, setCountry] = useState("")
    const [email, setEmail] = useState("")
    const [interested, setInterested] = useState("")
    const [message, setMessage] = useState("")
    const [name, setName] = useState("")
    const [phoneNumber, setPhoneNumber] = useState("")
    const [website, setWebsite] = useState("")

    const [submitForm, { loading }] = useMutation(SUBMIT_FORM);

    function handleSubmit(event) {
        event.preventDefault();
        if (loading) return;

        submitForm({
            variables: {
                budget: budget,
                companyName: companyName,
                country: country,
                email: email,
                interested: interested,
                message: message,
                name: name,
                phoneNumber: phoneNumber,
                website: website,
            }
        }).then((response) => {
            console.log('done', response);
            Array.from(document.querySelectorAll("input, textarea")).forEach(
                input => (input.value = "")
            );
        }).catch(error => {
            console.error(error);
        })
    }

    useLayoutEffect(() => {
        let timeline = anime.timeline({
            easing: 'linear',
            autoplay: true,
        });
        timeline.add({
            targets: '.container',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            scale: [0.8, 1],
            duration: 1500,
            delay: 500
        });
        timeline.add({
            targets: '.css--fadedtext',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            translateX: ["-100%", 0],
            duration: 500
        });
    }, [])

    return (
        <div className="relative pt-5 min-h-screen justify-center">
            <div className="container mx-auto px-4 h-full flex flex-col justify-center md:items-center pt-80 md:pt-20">
                <div className="grid grid-cols-1 gap-4 padding-top text-5xl glitch" data-glitch="Contact Us">
                    Contact Us
                </div>

                <div className="flex flex-col-reverse lg:flex-row sm:pt-0 sm:-mx-4 md:py-4">
                    <form method="post" onSubmit={handleSubmit} className="px-4 w-full lg:w-2/3 mb-20 sm:mb-0">
                        <div className="grid xs:grid-cols-1 sm:grid-cols-2 gap-4">
                            <div>
                                <input className="css--form-style" name="name" type="text" placeholder="Your Name: *" required autoComplete="off" onChange={(e) => {setName(e.target.value)}}/>
                            </div>
                            <div>
                                <input className="css--form-style" name="email" type="email" placeholder="E-Mail: *" required autoComplete="off" onChange={(e) => {setEmail(e.target.value)}}/>
                            </div>
                            <div>
                                <input className="css--form-style" name="phone" type="tel" placeholder="Phone Number:" autoComplete="off" onChange={(e) => {setPhoneNumber(e.target.value)}}/>
                            </div>
                            <div>
                                <input className="css--form-style" name="country" type="text" placeholder="City, Country:" autoComplete="off" onChange={(e) => {setCountry(e.target.value)}}/>
                            </div>
                            <div>
                                <select className="css--form-style" name="budget" onChange={(e) => {setBudget(e.target.value)}}>
                                    <option data-display="Budget">Budget:</option>
                                    <option value="100-1000">&euro;100 - &euro;1000</option>
                                    <option value="1000-5000">&euro;1000 - &euro;5000</option>
                                    <option value="5000-10000">&euro;5000 - &euro;10000</option>
                                    <option value="10000-50000">&euro;10000 - &euro;50000</option>
                                    <option value="50000+">&euro;50000 and up</option>
                                </select>
                            </div>
                            <div>
                                <select className="css--form-style" name="interested" onChange={(e) => {setInterested(e.target.value)}}>
                                    <option data-display="Interested For">Interested For:</option>
                                    <option value="branding">Branding</option>
                                    <option value="web-development">Web Development</option>
                                    <option value="kyc-aml">KYC/AML</option>
                                    <option value="saas-development">SaaS Development</option>
                                    <option value="graphic-design">Graphic Design</option>
                                </select>
                            </div>
                            <div>
                                <input className="css--form-style" name="company" type="text" placeholder="Company Name:" autoComplete="off" onChange={(e) => {setCompanyName(e.target.value)}} />
                            </div>
                            <div>
                                <input className="css--form-style" name="website" type="text" placeholder="Website:" autoComplete="off" onChange={(e) => {setWebsite(e.target.value)}} />
                            </div>

                        </div>
                        <div className="grid grid-cols-1 gap-4 pt-4">
                            <textarea className="css--form-style" name="message" placeholder="Tell Us Everything *" required onChange={(e) => {setMessage(e.target.value)}} />
                        </div>
                        <div className="grid md:grid-cols-4 sm:grid-cols-1 my-4 text-center">
                            <button className="btn px-5" type="submit">Submit</button>
                        </div>
                    </form>
                    <div className="px-4 w-full lg:w-1/3 xl:w-1/3 sm:mt-0 mb-8 lg:mb-0">
                        <div className="col-lg-4 mt-lg-0">
                            <div className="section css--letstalk-contacts p-4">
                                <h6 className="mb-3 text-white">Call Us</h6>
                                <p className="mb-0">+371 29 33 1911</p>
                                <p className="mb-4">+371 29 29 2929</p>
                                <h6 className="mb-3 text-white">Visit Us</h6>
                                <p className="mb-4">Kalnciema iela 207, Riga, LV-1046</p>
                                <h6 className="mb-3 text-white">Email Us</h6>
                                <p className="mb-0">info@kalmars.lv</p>
                                <p className="mb-0">it@kalmars.lv</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
