import React, {useEffect, useState} from "react";

const WhatIsKyc = () => {
    const [classState, setClassState] = useState('');

    useEffect(() => {
        setClassState('css--what-is-kyc-anim')
    }, [])

    return (
        <div id="kyc" className="flex justify-center content-center h-screen">
            <div className="flex justify-center items-center flex-wrap overflow-hidden">
                <div className="container" style={{zIndex:'10'}}>
                    <div className="md:grid-cols-3 grid-cols-1 gap-4 flex flex-col-reverse md:flex-row justify-center">
                        <div className="col-span-1 md:col-span-2 flex justify-center">
                            <div className="flex flex-col justify-center relative">
                                <div className="css--kyc-header-second mb-8">What is KYC?</div>
                                <div>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiuxsod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. </div>
                            </div>
                        </div>
                        <div className="flex justify-center items-center  w-7/12 md:w-full m-auto">
                            <div className={'css--what-is-kyc-triangle ' + classState}  style={{minHeight:'20vh'}}>
                                <span/>
                                <span/>
                                <span/>
                                <span/>
                                <span/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default WhatIsKyc;
