import React from 'react'

const KYCNavigation = (props) => {
    return <button onClick={() => props.addTrip(props.title)} className="mx-3" style={{height: '13px'}}>
        <div className={props.getState === props.title? "navigation-button-kyc active" : "navigation-button-kyc"} />
        <div className="navigation-text-kyc">{props.title}</div>
    </button>
}

export default KYCNavigation
