import React, {useLayoutEffect} from "react";
import {graphql, useStaticQuery} from "gatsby";
import anime from "animejs";

export default function Projects() {
    const data = useStaticQuery(graphql`
    query {
      allWpPost(
        sort: {fields: [date], order: DESC}
        filter: {categories: {nodes: {elemMatch: {name: {eq: "projects"}}}}}
      ) {
          nodes {
            id
            title
            slug
            excerpt
            date(formatString: "YYYY/MM/DD")
            tags {
              nodes {
                name
              }
            }
            featuredImage {
              node {
                sourceUrl
              }
            }
          }
      }
    }
  `)

    useLayoutEffect(() => {
        let timeline = anime.timeline({
            easing: 'linear',
            autoplay: true,
        });
        timeline.add({
            targets: '#projects',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            scale: [0.8, 1],
            duration: 1500,
            delay: 500
        });
        timeline.add({
            targets: '.css--fadedtext',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            translateX: ["-100%", 0],
            duration: 500
        });
    }, [])

    return (
        <div id="projects" className="pt-14 relative">
            <div className="mt-40 sm:mt-0 md:px-0 container px-4 mt-18 mx-auto">
                <div className="text-5xl glitch text-center " data-glitch="Success stories">
                    Success stories
                </div>
                <div className="grid md:grid-cols-1 gap-4 mt-12 relative pb-28">
                    {data.allWpPost.nodes.map((node,index) => (
                        <div className="relative pb-4 md:pb-40" key={index}>
                            <div className={"flex md:justify-start justify-center md:items-start items-center"} key={index}>
                                <div className={"rounded w-full css--projects-bg md:grid "}>
                                    <div className={"css--projects-photo " + (index % 2 ? "css--projects-photo-left" : "css--projects-photo-right")}>
                                        <img className="object-contain h-full w-full" src={node.featuredImage.node.sourceUrl} alt="Project image"/>
                                    </div>
                                    <div className={(index % 2 ? "css--projects-text-right" : "css--projects-text-left")}>
                                        <div className="px-6 pt-4 pb-2 flex flex-row">
                                            {node.tags.nodes.map((tag, i) => (
                                                <span className="text-center mr-5" key={i}>
                                                    <img src={`/img/develop_stack/${tag.name}.svg`} style={{height: "40px", margin: "10px auto"}} alt={tag.name} title={tag.name}/>
                                                        {tag.name}
                                                </span>
                                            ))}
                                        </div>
                                        <div className="px-6 py-4">
                                            <div className="font-bold text-white text-3xl mb-2">{node.title}</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>

            </div>
        </div>
    )
}
