import React from "react";

const ServicesKyc = ({nextPage}) => {
    return (
        <div id="kyc" className="flex justify-center content-center h-screen">
            <div className="flex justify-center items-center flex-wrap overflow-hidden">
                <div className="container" style={{zIndex: '10'}}>
                    <div className="grid grid-cols-1 gap-4 flex justify-center">
                        <div className="flex justify-center">
                            <div className="css--kyc-header-second mb-8">Services</div>
                        </div>
                    </div>
                    <div className="grid md:grid-cols-2 sm:grid-cols-1 md:gap-4 ">
                        <div className="flex justify-center">
                            <div className="w-full mx-auto px-8 md:p-8">

                                <Accordion>
                                    <AccordionItem toggle="section-1">Consulting</AccordionItem>
                                    <AccordionPanel id="section-1">
                                        <p className="mb-4">
                                            Before any type of development it's crucial to understand the
                                            existing business process which the client has. This process also involves the
                                            best possible solution proposal based on your business needs. *book a free
                                            appointment now!
                                        </p>

                                    </AccordionPanel>
                                    <AccordionItem toggle="section-2">Sanction policy legal document development</AccordionItem>
                                    <AccordionPanel id="section-2">
                                        <p>
                                            Internal document
                                            package will be prepared and showcased to the business. This document
                                            package plays crutial role to stay compliant in front of banks and legal
                                            regulations.
                                        </p>

                                    </AccordionPanel>
                                </Accordion>
                            </div>
                        </div>
                        <div className="flex justify-center">
                            <div className="w-full mx-auto px-8 md:p-8">

                                <Accordion>
                                    <AccordionItem toggle="section-1">Digitalize KYC/AML procedure</AccordionItem>
                                    <AccordionPanel id="section-1">
                                        <p className="mb-4">
                                            Based on business requirements next step is
                                            the implementation of the working solution in your system or else we can
                                            provide our working solution which will perform automatically all of the required
                                            steps. Starting from checking if the customer doesn't exist in the sanction list to
                                            knowing what is the UBO together with the credit score of a company.
                                        </p>
                                    </AccordionPanel>
                                </Accordion>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default ServicesKyc;

/* Logic */

const Context = React.createContext({});

function Accordion({children}) {
    const [selected, setSelected] = React.useState();

    const toggleItem = React.useCallback(
        (id) => () => {
            setSelected((prevState) => (prevState !== id ? id : ''));
        },
        [],
    );
    return (
        <Context.Provider value={{selected, toggleItem}}>
            {children}
        </Context.Provider>
    );
}

//custom hook to consume all accordion values
const useAccordion = () => React.useContext(Context);

const style = {
    item: `block focus:outline-none text-white text-base font-bold my-2 p-3`,
    panel: `overflow-hidden md:overflow-x-hidden text-sm text-white transition-height ease duration-300`,
};

function AccordionItem({toggle, children}) {
    const {selected, toggleItem} = useAccordion();
    return (
        <div role="button" onClick={toggleItem(toggle)} className={style.item}>
            {children}
            <span className="float-right">
        {selected === toggle ? <AngleUpIcon/> : <AngleDownIcon/>}
      </span>
        </div>
    );
}

function AccordionPanel({children, id}) {
    const {selected} = useAccordion();
    const ref = React.useRef();
    const inlineStyle =
        selected === id ? {height: ref.current?.scrollHeight} : {height: 0};

    return (
        <div ref={ref} id={id} className={style.panel} style={inlineStyle}>
            {children}
        </div>
    );
}

const AngleUpIcon = () => (
    <svg width="25" height="25" viewBox="0 0 25 25" fill="none" xmlns="http://www.w3.org/2000/svg">
        <line x1="25" y1="12.5" y2="12.5" stroke="white"/>
    </svg>

);

const AngleDownIcon = () => (
    <svg width="25" height="25" viewBox="0 0 25 25" fill="none" xmlns="http://www.w3.org/2000/svg">
        <line x1="12.5" y1="2.18557e-08" x2="12.5" y2="25" stroke="white"/>
        <line x1="25" y1="12.5" y2="12.5" stroke="white"/>
    </svg>

);

