import React from "react";

const HeroKyc = ({nextPage}) => {
    return (
        <div id="kyc" className="flex justify-center pt-40 md:pt-0 md:items-center flex-wrap h-screen overflow-hidden">
            <div className="container" style={{zIndex: '10'}}>
                <div className="grid md:grid-cols-2 grid-cols-1 md:gap-4 flex flex-col-reverse justify-center">
                    <div className="overflow-hidden flex justify-center md:justify-end md:items-end items-center flex-col-reverse md:flex-col">
                        <div className="flex flex-col justify-start relative pt-40 md:pt-0">
                            <div className="css--kyc-header-first mb-8 css--kyc-transparent">KYC</div>
                        </div>
                        <div className="overflow-hidden flex justify-end">
                            <div className="w-100 px-4 md:px-0">
                                Are local banks bothering you to provide documents to them about your customers or internal sanction list policy or else they are going to close your bank account? Well, it's related to this topic as currently, it's a must to perform due diligence for your customers and also to perform sanction screening. But where is the problem? The problem is the time, resources that is required to perform it correctly. But don't worry as we have a solution for it!
                            </div>
                        </div>
                    </div>
                    <div className="overflow-hidden flex justify-center md:justify-center md:items-start items-center flex-col">
                        <div className="flex flex-col justify-start relative">
                            <div className="css--kyc-header-first mb-8">
                                <span className="css--kyc-and">/</span>
                                <span className="css--kyc-transparent md:ml-14">AML</span>
                            </div>
                        </div>
                        <div className="w-3/5 overflow-hidden md:justify-end justify-center flex mt-8">
                            <button className="btn px-9" onClick={nextPage}>LEARN MORE</button>
                        </div>
                    </div>
                </div>
            </div>

        </div>
    )
}
export default HeroKyc;
