import * as React from "react"

import "../style/global.scss";
import LetsTalk from "../layout/LetsTalk";
import Projects from "../layout/Projects";
import HeroBlock from "../layout/HeroBlock";
import AddTripButton from "../components/elements/condButton";
// import WeAreBlock from "../layout/WeAre";
import FadedText from "../components/helpers/FadedText";
import KYC from "../layout/KYC";
import anime from "animejs";
import TextScramble from "../components/helpers/TextScramble";
import Navigation from "../components/elements/Navigation";
import {useCallback, useEffect, useLayoutEffect, useState} from "react";
import {Link} from "gatsby";

const IndexPage = () => {
    require("../style/hero_block.scss");
    require("../style/we_are_block.scss");
    require("../style/kyc_block.scss");
    require("../style/projects.scss");
    const [page, setPage] = useState("01");
    const [loading, setLoading] = useState(true)

    function getRandomInt(min, max) {
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min + 1)) + min;
    }

    const handlePageChange = (e) => {
        console.log(loading)
        if (e.deltaY < 0 && window.scrollY === 0 && !loading) {
            console.log(parseInt(page));
            setLoading(true)
            if (parseInt(page) !== 1) {
                setPage("0" + (parseInt(page) - 1))
            }
        } else if (e.deltaY > 0 && (window.innerHeight + window.scrollY) >= (document.body.offsetHeight - 10) && !loading) {
            console.log(parseInt(page));
            setLoading(true)
            if (parseInt(page) !== 4) {
                setPage("0" + (parseInt(page) + 1))
            }
        }
    }

    useLayoutEffect(() => {
        console.log(123)
        document.addEventListener("wheel", handlePageChange);
        return () => {
            document.addEventListener("wheel", handlePageChange);
        };
    }, [loading]);

    useLayoutEffect(() => {
        let timeline = anime.timeline({
            easing: 'linear',
            autoplay: true,
        });
        timeline.add({
            easing: 'easeOutExpo',
            autoplay: true,
            targets: '.css--anim-footer',
            translateX: ["-100%", 0],
            duration: 1500,
            delay: 1000
        });
        timeline.add({
            targets: '.css--page-number',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            scale: [0.8, 1],
            duration: 500,
        });
        timeline.finished.then(() => {
            console.log('DONEEEE');
            setLoading(false)
        })

        let elements = document.getElementsByClassName('text-scramble');
        Array.prototype.forEach.call(elements, function (el) {
            let glitch_text = new TextScramble(el)
            const next_update = () => {
                glitch_text.setText().then(() => {
                    setTimeout(next_update, 13000 + getRandomInt(0, 5000))
                })
            }
            next_update();
        });
    }, [page]);

    return (
        <main>
            <title>/KALMARS/ - WEB Development | KYC/AML | Or anything complex</title>
            <div id="main-wrapper relative">
                <div className="absolute css--page-number" style={{left: "10%", top: "5%"}}>{page}</div>
                <Navigation parentCallback={setPage}/>
                <div>
                    <div className="navigation-scrollbar">
                        <AddTripButton title={'01'} getState={page} nextPage={() => setPage('01')}/>
                        <AddTripButton title={'02'} getState={page} nextPage={() => setPage('02')}/>
                        <AddTripButton title={'03'} getState={page} nextPage={() => setPage('03')}/>
                        <AddTripButton title={'04'} getState={page} nextPage={() => setPage('04')}/>
                        {/*<AddTripButton title={'05'} getState={page} nextPage={() => setPage('05')}/>*/}
                    </div>
                </div>
                <div className="css--anim-footer fixed grid grid-cols-12 w-full justify-between" style={{left: "0", zIndex: "50"}}>
                    <div className="md:col-span-12 col-span-3 pl-8 md:pl-0 pt-8 md:pt-12">
                        <Link to="/">
                            <img src="/img/logo/logo_text_White-full.svg" style={{width: "100%", opacity: 0.2}} className="mx-4 css--navbar-logo md:float-right" alt="Kalmars Logo"/>
                        </Link>
                    </div>
                    <div className="md:col-span-12 col-span-3 css--writing-mode md:pl-4 md:pb-8 flex justify-center md:items-start items-center text-right hidden md:block">
                        info@kalmars.lv
                    </div>
                </div>

                {page === '01' &&
                <section>
                    <HeroBlock nextPage={() => setPage('02')}/>
                </section>
                }
                {/*{page === '02' &&*/}
                {/*<section>*/}
                {/*    <FadedText text={'DISCOVER'} direction="left"/>*/}
                {/*    <WeAreBlock nextPage={() => setPage('03')}/>*/}
                {/*</section>*/}
                {/*}*/}
                {page === '02' &&
                <section>
                    <FadedText text={'DISCOVER'} direction="left"/>
                    <Projects/>
                </section>
                }
                {page === '03' &&
                <section>
                    <FadedText text={'EXPAND'} direction="left"/>
                    <KYC/>
                </section>
                }
                {page === '04' &&
                <section>
                    <FadedText text={'EXPAND'} direction="left"/>
                    <LetsTalk/>
                </section>
                }
            </div>
        </main>
    )
}

export default IndexPage
