import React from "react";

const SolveKyc = ({nextPage}) => {
    return (
        <div id="kyc" className="flex justify-center content-center h-screen">
            <div className="flex justify-center items-center flex-wrap overflow-hidden">
                <div className="container" style={{zIndex: '10'}}>
                    <div className="grid grid-cols-1 gap-4 flex justify-center">
                        <div className="flex justify-center">
                            <div className="css--kyc-header-second mb-8">What risks you can reduce?</div>
                        </div>
                    </div>
                    <div className="grid md:grid-cols-3 grid-cols-1 mt-12">
                        <div className="mx-4">
                            <ul className="studio-list">
                                <li><p>Working with clients who exist
                                    in sanction list databases</p></li>
                                <li><p>Bad relationship with banks</p></li>

                            </ul>
                        </div>
                        <div className="mx-4">
                            <ul className="studio-list">
                                <li><p>Not knowing who is UBO</p></li>
                                <li><p>Not knowing the credit
                                    score limit to a partner</p></li>

                            </ul>
                        </div>
                        <div className="mx-4">
                            <ul className="studio-list">
                                <li><p>Getting a fine on non-compliance procedure</p></li>

                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default SolveKyc;
